@import (less) "color.less";

:host {
    position:relative;
    display: block;
}

:host(.ng-invalid.nbp-show-validation) {
  .textfield-box {
      border: 2px solid @red !important;
      -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
      &:focus {
          border: 2px solid @red;
      }
  }
}

.dropdown-menu {
	box-shadow: none !important;
}
.uib-datepicker-popup {
	z-index: 9999 !important;
	overflow: hidden;
	.btn {
		&:focus {
			outline: none !important;
		}
	}
}
.data-green{
	border: 2px solid @light_green !important;
}
.datepicker-container {
	position: relative;
	max-width: 300px;
	margin-bottom: 1.563em;
}
.datepicker-label {
	color: @gray;
	font-size: 0.750em;
	margin: 0 0 0 0.750em !important;
}
.datepicker-input-box {
	margin: 0.625em 0 0.625em 0;
	padding: 0.375em 1em 0.375em 1em;
	color: @gray;
	border: 1px solid @light_gray;
	border-radius: 3px;
	&::-ms-clear {
		display: none;
	}
	&:focus {
		border: 2px solid @light_green;
		outline: 0;
		box-shadow: none !important;
	}
	&[disabled] {
		cursor: auto;
		background-color: @white;
	    opacity:  0.5;
	}
}
.has-error {
	.datepicker-input-box {
		border: 2px solid @red;
		-webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
		box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
		&:focus {
			border: 2px solid @red;
		}
	}
}
.datepicker-error-message {
	color: @red;
	font-size: 0.750em;
	margin-top: 0;
	margin-bottom: 0.625em;
	opacity: 1;
}
.datepicker-error-message.ng-hide-add {
	transition: all ease-out 0.25s;
}
.datepicker-error-message.ng-hide-remove {
	transition: all ease-out 0.25s;
}
.datepicker-error-message.ng-hide {
	opacity: 0;
	margin-top: -0.625em;
}
.datepicker-button {
	display: inline-block;
	position: absolute;
	cursor: pointer;
	font-size: 1.500em;
	color: @gray;
	margin-top: -1.65em;
	right: 0.625em;
    &[role=""] {
        cursor: default;
		opacity: 0.50;
    }
}
.uib-datepicker-popup.dropdown-menu {
	border: none !important;
	border-radius: 0 !important;
	padding: 0 !important;
}
.uib-day {
	min-width: 42px;
	max-width: 42px;
}
.uib-daypicker {
	width: 300px !important;
	.btn {
		border-radius: 0 !important;
	}
	#datepicker-header {
		border-top: 1px solid @light_gray;
		.btn {
			&:focus {
				background-color: @darkest_gray;
				color: @white;
				border-color: @darkest_gray;
			}
			&:active {
				background-color: @darkest_gray;
				color: @white;
				border-color: @darkest_gray;
			}
			&:hover {
				background-color: @darkest_gray;
				color: @white;
				border-color: @darkest_gray;
			}
		}
	}
	.pull-left {
		padding-top: 8px;
		padding-bottom: 8px;
		min-width: auto;
		color: @gray;
		border-top: none;
		border-bottom: none;
		border-left: none;
		border-right: none;
	}
	.border-r-l {
		border-left: 1px solid @light_gray !important;
		border-right: 1px solid @light_gray !important;
	}
	.border-r {
		border-right: 1px solid @light_gray !important;
	}
	.uib-title {
		padding-top: 8px;
		padding-bottom: 8px;
		min-width: auto;
		border: none;
		color: @gray;
		font-size: 0.84em;
		cursor: default;
		pointer-events: none;
		text-transform: uppercase;
		strong {
			font-weight: normal !important;
		}
	}
	.pull-right {
		padding-top: 8px;
		padding-bottom: 8px;
		min-width: auto;
		color: @gray;
		border-top: none;
		border-bottom: none;
		border-left: none;
		border-right: none;
	}
	.border-l {
		border-left: 1px solid @light_gray !important;
	}
	.icon-arrow-left {
		display: block;
		margin-top: 4px;
	}
	.icon-arrow-right {
		display: block;
		margin-top: 4px;
	}
	.size-icon {
		font-size: 20px;
	}
	#datepicker-weeks {
		text-transform: uppercase;
		color: @gray;
		height: 2.625em;
		background-color: @darkest_white;
		border: 1px solid @light_gray;
		border-right: 2px solid @light_gray;
	}
}
.uib-weeks {
	border-right: 1px solid @light_gray;
	.btn {
		color: @gray;
		background-color: @lightest_gray;
		padding: 0;
		height: 42px;
		border-top: none;
		border-right: none;
		&:focus {
			background-color: @darkest_gray;
			color: @white;
			border-color: @darkest_gray;
		}
		&:active {
			background-color: @darkest_gray;
			color: @white;
			border-color: @darkest_gray;
		}
		&:hover {
			background-color: @darkest_gray;
			color: @white;
			border-color: @darkest_gray;
		}
	}
	.btn-info {
		border-color: @light_gray;
	}
	.current {
		color: @white;
		background-color: @gray;
		border: 1px solid @gray;
	}
	.text-info {
		color: @white;
	}
	.secondary {
		color: @light_gray;
		background-color: @white;
		.text-muted {
			color: @light_gray;
		}
	}
	.last-day {
		border-right: 1px solid @light_gray;
	}
	.btn.active {
		background-color: @green;
		border-color: @green;
		box-shadow: none;
		color: @white;
		.text-info {
			color: @white;
		}
	}
}
.uib-datepicker-popup.ng-enter {
	-webkit-transition: opacity .25s ease-in-out;
	-o-transition: opacity .25s ease-in-out;
	transition: opacity .25s ease-in-out;
	opacity: 0;
}
.uib-datepicker-popup.ng-leave {
	-webkit-transition: opacity .25s ease-in-out;
	-o-transition: opacity .25s ease-in-out;
	transition: opacity .25s ease-in-out;
	opacity: 1;
}
.uib-datepicker-popup.ng-enter.ng-enter-active {
	opacity: 1;
}
.uib-datepicker-popup.ng-leave.ng-leave-active {
	opacity: 0;
}

.yellow-border {
    border:2px solid @yellow;
}







.rightDate {
    .uib-datepicker-popup.dropdown-menu {
      padding: 15px 28px !important;
      top: -114px !important;
      overflow: visible;
    }
  
    .container-row {
        border-right: 1px solid @gray;
    }
    
    .uib-daypicker::after {
        bottom: 82px;
        left: -43px!important;
        right: auto!important;
        transform: rotate(-45deg)!important;
    }
  
}

.leftDate {
    .uib-datepicker-popup.dropdown-menu {
      padding: 15px 28px !important;
      top: -114px !important;
      overflow: visible;
    }
  
	.container-row {
	    border-right: 1px solid @gray;
	}
  
}

 @media (min-width:1200px) {
 	.rightDate {
		.uib-datepicker-popup.dropdown-menu {
      		left: 277px !important;
		}
	}

 	.leftDate {
		.uib-datepicker-popup.dropdown-menu {
      		left: -360px !important;
		}
	}

}

 @media (max-width:1199px) {
  	.rightDate {
		.uib-datepicker-popup.dropdown-menu {
			left:309px !important;
		}
	}

 	.leftDate {
		.uib-datepicker-popup.dropdown-menu {
      		left: -360px !important;
		}
	}

}

@media (max-width:991px) {
 	.rightDate {
		.uib-datepicker-popup.dropdown-menu {
			left:356px !important;
		}
	}

 	.leftDate {
		.uib-datepicker-popup.dropdown-menu {
      		left: -360px !important;
		}
	}
}

